.youtube {
    text-align: center;
}

ul.features {
    display: -moz-inline-flex;
    display: -webkit-inline-flex;
    display: -ms-inline-flex;
    display: inline-flex;
    cursor: default;
    list-style: none;
    padding-left: 0;
}

.features li {
    text-align: center;
}

.icon-major {
    display: inline-block;
    font-size: 3rem;
}

.wrapper-major {
    text-align: center;
    display: block;
    border: solid 1px #2C3E50;
    color: #b37e7c;
    width: 5.25rem;
    height: 5.25rem;
    line-height: 5rem;
    border-radius: 5rem;
    margin: auto;
}

.iframe-container {
    position: relative;
    width: 100%;
    padding-bottom: 56.25%;
    height: 0;
}

.iframe-container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}